<template>
    <div style="padding: 10px">
        <p>received data: <strong>{{params.pluginName}}</strong></p>
        <p class="text-info">select a item to return</p>
        <select v-model="item" class="form-control">
            <option value="Modal">Modal</option>
            <option value="Alert">Alert</option>
            <option value="Mask">Mask</option>
            <option value="Toast">Toast</option>
        </select>
        <p class="pull-right" style="margin-top: 20px;">
            <button type="button" class="btn btn-default" @click="ok"><i class="fa fa-fw fa-check"></i> OK</button>
        </p>

    </div>
</template>

<script>
    export default {
        name: "Modal",
        props: ['params'],
        data(){
            return {
                item: 'Modal'
            }
        },
        methods: {
            ok(){
                let that = this;
                this.$vDialog.close({
                    item: that.item
                });
            }
        }
    }
</script>


