<template>
    <div class="container">
        <h2 class="page-header">Install</h2>
        <p>use <strong>npm</strong> to install plguin</p>
        <pre class="brush:bash">
            npm i v-dialogs --save
        </pre>
        <p>Include plugin in your <code>main.js</code> file</p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vDialogs from 'v-dialogs';

            ...

            Vue.use(vDialogs);
        </pre>

        <br>

        <h2 class="page-header">The Dialog Icon</h2>

        <p>the icons in alert dialog used are made by <a href="http://www.elegantthemes.com/blog/freebie-of-the-week/beautiful-flat-icons-for-free" target="_blank">Elegant Themes</a></p>
        <p>and control icon, toast icon used are come from <a href="http://www.iconfont.cn" target="_blank">IconFont</a></p>


        <br><br>

        <h2 class="page-header">Example / Use case</h2>
        <h4>Modal <small>open a component(page) in Modal dialog</small></h4>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js">
                this.$vDialog.modal(component, options);
                </pre>

                <div class="clearfix">
                    <div class="col-md-12">
                        <button type="button" class="btn btn-default" @click="callModal"><i class="fa fa-fw fa-window-maximize"></i> Open Modal</button>
                        <button type="button" class="btn btn-default" @click="code.modal = !code.modal"><i class="fa fa-fw fa-code"></i> Show/Hide code</button>
                    </div>
                </div>
            </div>
        </div>



        <div v-show="code.modal">
        <pre class="brush:js">
            import myComponent from './myComponent.vue';

            export default {
                methods: {
                    newUser(){
                        //open the new user form in Modal
                        this.$vDialog.modal(myComponent);
                    }
                }
            }
        </pre>
        <h4>other practical use case for Modal</h4>
        <pre class="brush:js">
            //parent component
            import myComponent from './myComponent.vue';
            export default {
                methods: {
                    save(){
                        this.$vDialog.modal(myComponent, {
                            width: 500,
                            height: 450,
                            messageType: 'success',
                            //when inner component call $vDialog.close({data}) to close modal
                            //callback will receive the return data
                            callback: function(data){
                                console.log(data);//{userFullName: 'TerryZeng'}
                            }
                            //send params to inner component
                            params: {
                                userName: 'Terry'
                            }
                        });
                    }
                }
            };

            //the myComponent.vue
            export default {
                props: ['params'],//receive parent parameters
                methods: {
                    save(){
                        console.log(this.params.userName);//Terry
                        let that = this;
                        this.$vDialog.alert('user info save success', function(){
                            //close modal and return data
                            this.$vDialog.close({ userFullName: that.params.userName + 'Zeng'});
                        }, {messageType: 'success'});
                    }
                }
            };
        </pre>
        </div>

        <br>
        <h4>Alert <small>open a message alert dialog, including info, warning, error, success, confirm types</small></h4>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js">
                //the message is support HTML code in Alert, Mask, Toast mode
                this.$vDialog.alert(message, callback, options);
                </pre>

                <div class="col-md-2">
                    <select v-model="alertType" class="form-control">
                        <option value="info">info</option>
                        <option value="warning">warning</option>
                        <option value="error">error</option>
                        <option value="success">success</option>
                        <option value="confirm">confirm</option>
                    </select>
                </div>
                <button type="button" class="btn btn-default" @click="callAlert"><i class="fa fa-fw fa-warning"></i> Open Alert</button>
                <button type="button" class="btn btn-default" @click="code.alert = !code.alert"><i class="fa fa-fw fa-code"></i> Show/Hide code</button>
            </div>
        </div>

        <div v-show="code.alert">
        <pre class="brush:js">
            export default {
                methods: {
                    save(){
                        this.$vDialog.alert('form data save < b>success< /b>!', function(){
                            //dialog close callback
                        }, {
                            messageType: 'success',
                            language: 'en'
                        });
                    }
                }
            }
        </pre>
        </div>

        <br>
        <h4>Mask <small>open a full screen mask</small></h4>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js">
                this.$vDialog.modal(message, callback, options);
                </pre>

                <div class="clearfix">
                    <div class="col-md-12">
                        <button type="button" class="btn btn-default" @click="callMask"><i class="fa fa-fw fa-object-ungroup"></i> Open mask</button>
                        <button type="button" class="btn btn-default" @click="code.mask = !code.mask"><i class="fa fa-fw fa-code"></i> Show/Hide code</button>
                    </div>
                </div>
            </div>
        </div>

        <div v-show="code.mask">
        <pre class="brush:js">
            export default {
                methods: {
                    save(){
                        this.$vDialog.mask('form data save success!', function(){
                            //dialog close callback
                        }, {
                            language: 'en'
                        });
                    }
                }
            }
        </pre>
        <h4>other practical use case for Mask</h4>
        <pre class="brush:js">
            export default {
                methods: {
                    save(){
                        let that = this;
                        //open a full screen mask
                        //using default text: 'Loading...'
                        let key = this.$vDialog.mask();
                        //submit to server side
                        axios({...}).then(function(resp){
                            //when submit success, close mask by key
                            that.$vDialog.close(null, key);
                        });
                    }
                }
            }
        </pre>
        </div>


        <br>
        <h4>Toast <small>open a corner dialog, have 4 message type and 6 corner position</small></h4>
        <div class="panel panel-default">
            <div class="panel-body">
                <pre class="brush:js">
                this.$vDialog.toast(message, callback, options);
                </pre>


                <div class="col-md-2">
                    <select v-model="position" class="form-control">
                        <option value="topLeft">topLeft</option>
                        <option value="topCenter">topCenter</option>
                        <option value="topRight">topRight</option>
                        <option value="bottomLeft">bottomLeft</option>
                        <option value="bottomCenter">bottomCenter</option>
                        <option value="bottomRight">bottomRight</option>
                    </select>
                </div>
                <div class="col-md-2">
                    <select v-model="toastType" class="form-control">
                        <option value="info">info</option>
                        <option value="warning">warning</option>
                        <option value="error">error</option>
                        <option value="success">success</option>
                    </select>
                </div>
                <button type="button" class="btn btn-default" @click="callToast"><i class="fa fa-fw fa-paper-plane-o"></i> Open Toast</button>
                <button type="button" class="btn btn-default" @click="code.toast = !code.toast"><i class="fa fa-fw fa-code"></i> Show/Hide code</button>
            </div>
        </div>

        <div v-show="code.toast">
        <pre class="brush:js">
            export default {
                methods: {
                    save(){
                        this.$vDialog.toast('you got a new message!', function(){
                            //dialog close callback
                        }, {
                            messageType: 'warning',
                            language: 'en',
                            closeTime: 3 //auto close dialog time(second)
                        });
                    }
                }
            }
        </pre>
        </div>

        <br><br>

        <h2 class="page-header">Options</h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th>name</th>
                    <th>type</th>
                    <th>description</th>
                    <th>default</th>
                    <th>scope</th>
                </tr>
            </thead>
            <tbody>
            <tr>
                <td>message</td>
                <td>string</td>
                <td>The message show in dialog, support html tags</td>
                <td>undefined</td>
                <td>Alert, Mask, Toast</td>
            </tr>
            <tr>
                <td>messageType</td>
                <td>string</td>
                <td>
                    dialog message type
                    <ul>
                        <li><code>'info'</code></li>
                        <li><code>'warning'</code></li>
                        <li><code>'error'</code></li>
                        <li><code>'success'</code></li>
                        <li><code>'confirm'</code> only work on Alert mode</li>
                    </ul>
                </td>
                <td>'info'</td>
                <td>Alert, Toast</td>
            </tr>
            <tr>
                <td>title</td>
                <td>string | boolean</td>
                <td>dialog title bar text or title bar show control, set <code>false</code> to close title bar</td>
                <td>'vDialog'</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>width</td>
                <td>number</td>
                <td>dialog width</td>
                <td>700</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>height</td>
                <td>number</td>
                <td>dialog height</td>
                <td>400</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>params</td>
                <td>object</td>
                <td>send parameters to component and use props to receive this params in component</td>
                <td>undefined</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>dialogCloseButton</td>
                <td>boolean</td>
                <td>the close dialog button show or not</td>
                <td>true</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>dialogMaxButton</td>
                <td>boolean</td>
                <td>click to make dialog maximize, and click again to restore</td>
                <td>true</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>position</td>
                <td>string</td>
                <td>corner position type for dialog
                    <ul>
                        <li><code>'topLeft'</code></li>
                        <li><code>'topCenter'</code></li>
                        <li><code>'topRight'</code></li>
                        <li><code>'bottomLeft'</code></li>
                        <li><code>'bottomCenter'</code></li>
                        <li><code>'bottomRight'</code></li>
                    </ul>
                </td>
                <td>'bottomRight'</td>
                <td>Toast</td>
            </tr>
            <tr>
                <td>singletonKey</td>
                <td>string</td>
                <td>specified a key to make dialog singleton(the same key dialog will not open in the same time)</td>
                <td>undefined</td>
                <td>All</td>
            </tr>
            <tr>
                <td>language</td>
                <td>string</td>
                <td>dialog use language
                    <ul>
                        <li><code>'cn'</code></li>
                        <li><code>'en'</code></li>
                        <li><code>'jp'</code></li>
                    </ul>
                </td>
                <td>400</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>closeTime</td>
                <td>number | boolean</td>
                <td>auto close dialog time(seconds), set false to disable auto close</td>
                <td>false</td>
                <td>Modal</td>
            </tr>
            <tr>
                <td>cancelCallback</td>
                <td>number | boolean</td>
                <td>close dialog callback, trigger by some case: <br>
                    click the close button in top right corner(Model,Toast mode)<br>
                    click 'cancel' button in Alert mode ('confirm' message type)
                </td>
                <td>false</td>
                <td>Modal</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import modal from './Modal';
    export default {
        name: "Demo",
        data(){
            return {
                alertType: 'info',
                position: 'bottomRight',
                toastType: 'info',
                code: {
                    modal: false,
                    alert: false,
                    mask: false,
                    toast: false
                }
            }
        },
        methods:{
            callModal(){
                let that = this;
                this.$vDialog.modal(modal, {
                    width: 400,
                    height: 250,
                    title: 'vDialog Modal',
                    callback: function(data){
                        that.$vDialog.alert(`received message: <b>${data.item}</b>`);
                    },
                    params: {
                        pluginName: 'this plugin name is v-dialogs'
                    }
                });
            },
            callAlert(){
                this.$vDialog.alert(`This is a Alert dialog, messageType is ${this.alertType}`, null, {
                    messageType: this.alertType,
                    language: 'en'
                });
            },
            callMask(){
                this.$vDialog.mask(null,null,{
                    language: 'en',
                    closeTime: 3
                });
            },
            callToast(){
                let that = this;
                this.$vDialog.toast(`This is a Toast dialog, messageType is ${this.toastType}`,null,{
                    messageType: that.toastType,
                    language: 'en',
                    closeTime: 3,
                    position: that.position
                })
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style scoped>

</style>


